import {Component,  Input, OnInit} from '@angular/core';
import {AppContextService} from "../../../services/app-context.service";
import {IncomingPopupContext, PopupContext} from "../../../Classes/Classes";
import {WebRtcService} from "../../../services/web-rtc.service";
import {DatabaseService} from "../../../services/database.service";

@Component({
  selector: 'app-incoming-call-notification',
  templateUrl: './incoming-call-notification.component.html',
  styleUrls: ['./incoming-call-notification.component.css']
})
export class IncomingCallNotificationComponent implements OnInit {
    
    public currentCall = false;
    public contactName;
    @Input() public context : IncomingPopupContext;
    buttons = [
        {tip : 'Принять', icon : 'check_circle', color : '#00902e', listener : ()=> {
		this.onCancel('accepted');
		this.context.listener() ;
	    }},
	{tip : 'Отклонить', icon : 'highlight_off', color : '#9e1f00', listener : ($event)=> {
	    this.onCancel('denied');
	    }}
	];
  constructor(
      public appContext : AppContextService,
      public webRtcService : WebRtcService,
      public database : DatabaseService,
  ) {}
  
  
  
      onCancel(action){
      //Снятие признака активности предложение в базе данных
      this.context.desc && this.database.setDescriptorOptions({descriptor : this.context.desc, data : {active : false, action : action}} ) ;
      //Снятие активности контекста
      this.context.active = false;
      //Поиск индекса контекста в коллекции входящих вызовов
      let index = this.appContext.popups.value.findIndex(incoming => incoming.uid === this.context.uid);
      //Удаление входящего вызова из коллекции для удалении его с экрана пользователя
      if(index > -1){
	  this.appContext.setPopups({type : false, popup : undefined, index : index});
      }
      //Обновление интерфейса
      this.appContext.appChangeRef.detectChanges();
  }
    
    async onErrorContactImg(event){
	//Если контакт пользователя сам поменял изображение, то ссылка на него меняется.
	//Вылетает ошибка загрузкти ресурса, поэтому перегружаем ресурс из объекта User
	await this.database.onErrorContactImg(event, this.context);
	return true;
    }
  
  ngOnInit() {
      this.contactName = this.context.contact.name || this.context.contact.displayName || this.context.contact.phoneNumber || this.context.contact.email;
      //существует ли текущий вызов
      this.currentCall = !!Object.values(this.appContext.webRtcContexts.contexts.value).length ;
      //Запуск таймера удаления уведомления, на случай, если пользователь оставит его без внимание,
      //оно должно быть удалено авоматически.
      let timeId = window.setTimeout(()=>{
          window.clearTimeout(timeId);
          this.onCancel('ignored');
      }, [20000, 30000, 40000][parseInt(window.localStorage.getItem('dialogTimes'))]);//диапазон отображения диалогов
  }
}
